<template>
  <div>
    <div v-for="(p,i) in previews" :key="`preview${i}`" class="preview block" @click="linkto({path:`/article/${p.name}`})">
      <h3 class="title b">{{p.title}}</h3>
      <div class="prev-area block">
        <div class="plan-text line" v-html="p.content"></div>
        <div class="inner-block cover"></div>
      </div>
      <div class="line">
        <div class="line b">date: {{preview_dates[i]}}</div>
      </div>
      <div class="tags-container line">
        <div class="line b">tags: </div>
        <tag v-for="tag in p.tags" :key="`${p}-tag${tag}`" :tag="tag" class="line"></tag>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Tag from './Tag.vue'

export default {
  props:['max','previews'],
  components: {
    Tag
  },
  computed:{
    preview_dates(){
      return (this.previews)?this.previews.map(p=>{
        const d = p.date
        return `${d.getFullYear()}/${d.getMonth()+1}/${d.getDate()}`
      }):[]
    },
    ...mapState({
      lists:'lists',
      settings: 'settings'
    })
  } ,
  methods: {
    linkto(route){
      this.$router.replace(route)
    }
  }
}
</script>

<style scoped>
.preview {
  width: 100%;
  cursor: pointer;
  overflow: hidden;
}
.title{
  margin-block-end: 0.6em;
}
.prev-area>.cover {
  background-image: linear-gradient(to top, rgba(255,255,255,0.8),rgba(255,255,255,0.1),rgba(255,255,255,0));
}
.preview {
  border-bottom: 1px solid lightgray;
}
.plan-text {
  overflow: hidden;
  max-height: 3.8rem;
}
.line {
  padding: 3px;
  font-size: 0.86rem;
}
</style>


